
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
  name: 'goods',
  data: function () {
    return {
      index: 0,
      goods_id: 0,
      item_index: 0,
      page_goods_data: {},
      current_page: 0,
      goods_data_list: {
        name: '',
        content: '',
        price: '',
        mktprice: '',
        image_attach:[],
      },
      from: 0,
      last_page: 0,
      per_page: 0,
      to: 0,
      total: 0,
      swiper_list: [{
        url: '/home',
        img: 'https://static.vux.li/demo/2.jpg',
        title: '送你一朵小花'
      }, {
        url: '/home',
        img: 'https://static.vux.li/demo/2.jpg',
        title: '送你一朵小花'
      }, {
        url: '/home',
        img: 'https://static.vux.li/demo/2.jpg',
        title: '送你一朵小花'
      }, {
        url: '/home',
        img: 'https://static.vux.li/demo/2.jpg',
        title: '送你一朵小花'
      }]
    }
  },
  methods: {
    init_goods_page: function (init_data) {
      // console.log()
      if (!!init_data.goods_list.data && init_data.goods_list.data.length) {
        var page_goods_data = init_data.goods_list;
        this.current_page = page_goods_data.current_page;
        this.goods_data_list = page_goods_data.data[this.item_index];
        console.log(this.goods_data_list);
        this.from = page_goods_data.from;
        this.last_page = page_goods_data.last_page;
        this.per_page = page_goods_data.per_page;
        this.to = page_goods_data.to;
        this.total = page_goods_data.total;
      }
    }
  },
  created: function () {
    var query = this.$route.query;
    this.goods_id = query.goods_id;
    this.item_index = query.item_index;
    console.log(this.$store.state.goods);
    this.init_goods_page(this.$store.state.goods);
    console.log(this.$route.query);

  },
  components: {
    Swiper,
    Flexbox,
    FlexboxItem,
    XButton,
    Tab,
    TabItem,
    SwiperItem
  }
}




